<template>
	<view>
		<camera class="camera" v-if="showCamera" device-position="front" flash="off" @error="error"></camera>
		<image class="image" v-if="showImage" :src="photoPath" mode="widthFix"></image>
		<view class="operater-container">
			<button class="btn" type="primary" @tap="clickBtn" :disabled="!canCheckin">{{btnText}}</button>
			<button class="btn" type="warn" @tap="afresh" :disabled="!canCheckin">重拍</button>
		</view>
		<view class="notice-container">
			<text class="notcie">注意事项</text>
			<text class="desc">拍照签到的时候，必须要拍摄自己的正面照片，侧面照片会导致无法识别。另外，拍照的时候不要戴墨镜或者帽子，避免影响拍照签到的准确度</text>
		</view>
	</view>
</template>

<script>
	var QQMapWX = require("../../lib/qqmap-wx-jssdk.min.js");
	var qqmapsdk;
	export default {
		data() {
			return {
				canCheckin: true,
				photoPath: '',
				btnText: '拍照',
				showCamera: true,
				showImage: false
			}
		},
		onLoad:function(){
			qqmapsdk = new QQMapWX({
				key: "5WLBZ-TZWKW-KYDRU-OTCF7-7HYO3-LGFGF"
			})
		},
		methods: {
			clickBtn: function(){
				let that = this
				if(that.btnText=="拍照"){
					let take = uni.createCameraContext()
					take.takePhoto({
							quality:"high",
							success:function(resp) {
								that.btnText= "签到";
								that.photoPath= resp.tempImagePath;
								that.showCamera=false;
								that.showImage=true;
							}
					})
				}else{
					//签到
					uni.showLoading({
						title:"签到中，请稍等..."
					})
					//TODO 这里不能等签到成功再显示吗？
					setTimeout(function() {
						uni.hideLoading()
					}, 3000)
					//获取地理位置
					uni.getLocation({
						success: function(resp){
							let latitude = resp.latitude
							let longitude = resp.longitude
							console.log(latitude)
							console.log(longitude)
							qqmapsdk.reverseGeocoder({
								location: {
									latitude: latitude,
									longitude: longitude
								},
								success: function(resp){
									console.log(resp);
									let address = resp.result.address;
									let addressComponent = resp.result.address_component;
									let nation = addressComponent.national;
									let province = addressComponent.province;
									let city = addressComponent.city;
									let district = addressComponent.district;
								},
								fail: function(resp){
									uni.showToast({
										title: "定位失败",
										icon: "none"
									})
									console.log(resp)
								}
							})
							// qqmapsdk.reverseGeocoder({
							// 	location: {
							// 		latitude: latitude,
							// 		longitude: longitude
							// 	},
							// 	success: function(resp) {
							// 		console.log(resp.result);
							// 		let address = resp.result.address;
							// 		let addressComponent = resp.result.address_component;
							// 		let nation = addressComponent.nation;
							// 		let province = addressComponent.province;
							// 		let city = addressComponent.city;
							// 		let district = addressComponent.district;
							// 	},
							// 	fail: function(resp){
							// 		console.log(resp)
							// 	}
							// })

						}
					})
					
				}
				that.btnText='拍照';
				that.showCamera= true;
				that.showImage= false;
			}
		}
	}
</script>

<style lang="less">
	@import url("checkin.less");
</style>
